.main {
    height: 100vh;
    display: grid;
    grid-template-rows: 80px auto;

    .demo-header {
        display: grid;
        align-items: center;
        background-color: var(--color-1);
        color:var(--color-2);
        font-weight: var(--weight-2);
        margin: 0 0;
        line-height: 1em;
        padding-left: 5px;
    }
    
    .demo-content {
        display: grid;
        grid-template-columns: minmax(150px, 15%) auto;
        // grid-template-columns: 30vh auto;
    }
}